<template>
  <div class="newid" v-if="newID">
    <router-link
      class="fanhui iconfont icon-back_icon"
      tag="div"
      to="/"
    ></router-link>
    <div class="newidsons">
      <div class="imgback"></div>
      <div class="newidsons1">
        <img :src="newID.coverImgUrl" alt="" />
        <div class="imgback1">
          <div class="imgback2">
            <p>{{ newID.name }}</p>
            <p><span class="iconfont icon-bofang"></span>{{ newID.playCount }}</p>
          </div>
          <p class="imgbackp">
            <span>全部播放10首歌曲</span
            ><span
              class="iconfont icon-bofang"
              @click="$emit('play', mytracks)"
            ></span>
          </p>
        </div>
      </div>
    </div>
    <div class="newidson">
      <p>
        <span @click="piccomment = false">歌曲</span
        ><span @click="piccomment = true">评论</span
        ><span>{{ commentsall.length }}</span>
      </p>
      <transition
        enter-active-class="animate__animated animate__slideInUp"
        leave-active-class="animate__animated animate__slideOutDown"
      >
        <div>
          <newest-music
            v-show="!piccomment"
            v-for="track in mytracks"
            :key="track.id"
            :item="track"
            @play="$emit('play', $event)"
          ></newest-music>
          <comment
            :hotComments="hotComments"
            :comments="comments"
            v-show="piccomment"
            :indexnum="indexnum"
            @Newcomment="Newcomment"
            :commentsall="commentsall"
            :mvstr="mvstr"
          ></comment>
        </div>
      </transition>
    </div>
    <div></div>
    <div class="potcomment"></div>
  </div>
</template>
<script>
import NewestMusic from "@/components/NewestMusic";
import Comment from "@/components/Comment";
export default {
  data() {
    return {
      newID: null,
      mytracks: [],
      piccomment: false,
      comments: [],
      hotComments: [],
      indexnum: 0,
      mvstr:"按时间",
      commentsall: [],
    };
  },
  components: {
    NewestMusic,
    Comment,
  },
  beforeRouteEnter(to, from, next) {
    // ...
    next((vm) => {
      vm.$axios
        .get("/playlist/detail?id=" + vm.$route.params.userId)
        .then((d) => {
          vm.newID = d.data.playlist;
          console.log(d.data);
          vm.mytracks = d.data.playlist.tracks.map((data) => {
            data.song = {
              artists: data.ar,
              album: data.al,
              alias: [],
            };
            data.picUrl = data.al.picUrl;
            return data;
          });
          vm.newID = d.data.playlist;
          console.log(vm.mytracks);
        });
      vm.$axios
        .get("/comment/playlist?id=" + Number(vm.$route.params.userId))
        .then((data) => {
          vm.comments = data.data.comments;
          vm.hotComments = data.data.hotComments;
          vm.commentsall = vm.comments.concat(data.data.hotComments);
        });
    });
  },
  methods: {
     Newcomment() {
      function compare(property, desc) {
        return function (a, b) {
          var value1 = a[property];
          var value2 = b[property];
          if (desc == true) {
            // 升序排列
            return value1 - value2;
          } else {
            // 降序排列
            return value2 - value1;
          }
        };
      }
      // console.log(sdts.sort(compare("age",true)))
       if(this.indexnum == 0){
        this.indexnum = 1;
        this.mvstr="按热度"
        this.commentsall = this.commentsall.sort(compare("likedCount", false));
      }else if(this.indexnum == 1){
        this.indexnum = 0;
        this.mvstr="按时间"
        this.commentsall = this.commentsall.sort(compare("time", false));
      }
    },
    // Newcomment() {
    //   this.indexnum = 0;
    //   function compare(property, desc) {
    //     return function (a, b) {
    //       var value1 = a[property];
    //       var value2 = b[property];
    //       if (desc == true) {
    //         // 升序排列
    //         return value1 - value2;
    //       } else {
    //         // 降序排列
    //         return value2 - value1;
    //       }
    //     };
    //   }
    //   // console.log(sdts.sort(compare("age",true)))
    //   this.commentsall = this.commentsall.sort(compare("time", false));
    // },
    // Hotcomment() {
    //   this.indexnum = 1;
    //   function compare(property, desc) {
    //     return function (a, b) {
    //       var value1 = a[property];
    //       var value2 = b[property];
    //       if (desc == true) {
    //         // 升序排列
    //         return value1 - value2;
    //       } else {
    //         // 降序排列
    //         return value2 - value1;
    //       }
    //     };
    //   }
    //   // console.log(sdts.sort(compare("age",true)))
    //   this.commentsall = this.commentsall.sort(compare("likedCount", false));
    // },
  },
  beforeRouteLeave(to, from, next) {
    next((vm) => {
      vm.newID = null;
      vm.mytracks = null;
      vm.comments = null;
      vm.hotComments = null;
    });
  },
};
</script>
<style lang="less" scoped>
.newid {
  .fanhui {
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    font-weight: 600;
    font-size: 20px;
    z-index: 9999;
  }
  .search-input-butt {
    height: 5.867vw;
    color: #fff;
    position: absolute;
    top: 5px;
    z-index: 999;
    font-size: 4.267vw;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    line-height: 6vw;
    margin-left: 4vw;
  }
  .newidsons {
    height: 50vw;
    overflow: hidden;
    position: relative;
    .imgback {
      width: 100%;
      background-position: center;
      height: 50vw;
      filter: blur(30px);
      background-size: cover;
      background: url(../assets/hpn.jpg);
    }
    .newidsons1 {
      &:nth-child(2) {
        position: absolute;
        top: 25%;
        margin-left: 15px;
        margin-right: 10px;
        display: flex;
        align-items: center;
        color: #eee;
        img {
          width: 30%;
          border-radius: 50%;
        }
        .imgback1 {
          .imgback2{
            margin-left: 20px;
            font-size: 3.6vw;
            p{
              margin: 2vw 0 2vw 0;
            }
          }
          .imgbackp{
            &:nth-child(2) {
              position: absolute;
              top: 90%;
              right: 4%;
              display: flex;
              align-items: center;
              .icon-bofang {
                font-size: 8vw;
              }
              span {
                line-height: 10vw;
                &:first-child {
                  font-size: 5vw;
                }
              }
            }
          }
        }
      }
    }
  }
  .newidson {
    p {
      height: 40px;
      display: flex;
      align-items: center;
      margin: 0 0 0 3vw;
      border-bottom: 1px solid #ddd;
      span {
        margin-left: 1vw;
        font-size: 4vw;
        color: #666;
        &:first-child {
          margin-right: 6vw;
        }
        &:nth-child(2) {
          span {
            font-size: 3.2vw;
          }
        }
      }
    }
    div {
      p {
        &:first-child {
          font-size: 4vw;
          margin: 0.3vw 1.4vw 0.3vw 1.4vw;
        }
        & {
          font-size: 14px;
          margin: 0 5px;
        }
      }
      div {
        margin-left: 3vw;
      }
    }
  }
  .potcomment {
    height: 37px;
  }
}
</style>